import React from 'react';

const Marker = () => {
  window.onload = () => {
    let scene = document.querySelector('a-scene'); /* Apply to whole scene */

    navigator.geolocation.getCurrentPosition(function success(position) {
      let gps = document.createAttribute('gps-entity-place'),
        arjs = document.createAttribute('arjs'),
        welcome = document.getElementById('welcome');
      gps.value = `latitude: ${position.coords.latitude - 0.001}; longitude: ${
        position.coords.longitude - 0.001
      };`;

      arjs.value =
        'sourceType: webcam; sourceWidth: 1280; sourceHeight: 960; trackingMethod: best; debugUIEnabled: false;';

      scene.setAttributeNode(gps); /* Apply to whole scene */
      scene.setAttributeNode(arjs);
    });
  };
  return (
    <div>
      <a-scene
        vr-mode-ui="enabled: false"
        embedded
        arjs="sourceType: webcam; videoTexture: true; debugUIEnabled: false;"
      >
        <a-entity geometry="primitive: box" material="color: red"></a-entity>
        <a-text
          id="welcome"
          value="Welcome"
          scale="75 75 75"
          color="#000000"
          position="-30 0 -150"
        ></a-text>
        <a-camera gps-camera rotation-reader></a-camera>
      </a-scene>
    </div>
  );
};
export default Marker;
